<template>
  <div class="color-select-wrapper">
    <span v-if="showDesc">{{ label }}&nbsp;&nbsp;</span>
    <div class="color-select">
      <div
        @click="changePickerStatus"
        class="color-preview"
        :style="{ 'background-color': _color }"
      ></div>
      <div class="color-clear" @click="clean">
        <img src="@/assets/icon/clear_default.png" alt="" />
      </div>
    </div>
    <color-picker
      class="picker"
      v-show="pickerStatus"
      theme="light"
      :color="_color"
      @changeColor="selectedColor"
    />
  </div>
</template>

<script>
import util from "@/util/";
import colorPicker from "@caohenghu/vue-colorpicker";
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState, mapGetters } = createNamespacedHelpers(
  "componentStore"
);
const { rgb2hex } = util;
export default {
  name: "color-selector",
  components: {
    "color-picker": colorPicker
  },
  props: {
    color: {
      type: String,
      required: true
    },
    showDesc: {
      type: Boolean,
      default: true
    },
    label: {
      type: String,
      default: "颜色"
    }
  },
  data() {
    return {
      pickerStatus: false,
      defaultColor: "",
      localColor: null,
      debounceColorChange: null
    };
  },
  methods: {
    colorWidgetClick() {
      this.changePickerStatus();
    },
    changePickerStatus() {
      this.pickerStatus = !this.pickerStatus;
    },
    selectedColor(color) {
      let rgbStr = `rgb(${color.rgba.r},${color.rgba.g},${color.rgba.b})`;
      let hexStr = rgb2hex(rgbStr);
      this._color = `#${hexStr}`;
    },
    clean() {
      this._color = this.defaultColor;
      this.pickerStatus = false;
    }
  },
  computed: {
    ...mapGetters(["activatedComponent"]),
    _color: {
      get() {
        return this.localColor;
      },
      set(val) {
        this.localColor = val;
        // if (val !== this.color) {
        //   this.$emit("change", val);
        // }
      }
    }
  },
  watch: {
    localColor(val) {
      if (val !== this.color) {
        this.debounceColorChange(val);
      }
    }
  },
  mounted() {
    this.defaultColor = this.color;
    // 创建局部变量
    this.localColor = this.color;
    // 包装colorChange事件防止过度调用造成页面卡顿
    this.debounceColorChange = window.lodash.debounce(val => {
      this.$emit("change", val);
    }, 20);
  }
};
</script>
<style lang="stylus" scoped>
.color-select-wrapper
  position relative
  display flex
  align-items center
  .color-select
    display flex
    width 72px
    height 30px
    .color-preview
      width 50px
      height 30px
      border 1px solid $default-border-color
      border-top-left-radius 5px
      border-bottom-left-radius 5px
    .color-clear
      position relative
      left 0.5px
      width 22px
      height 30px
      border 1px solid $default-border-color
      border-top-right-radius 5px
      border-bottom-right-radius 5px
      text-align center
      line-height 27px
  .picker
    position absolute
    top 30px
    right 10px
</style>
