<template>
  <div class="slider-edit">
    <a-row class="row-wrapper">
      <a-col :span="5" style="margin-top: 8px;">
        <span>{{ opts.placeholder }}</span>
      </a-col>
      <a-col :span="12">
        <a-slider
          :min="opts.min"
          :max="opts.max"
          :step="opts.step"
          v-model="_value"
        />
      </a-col>
      <a-col :span="6" style="margin-left: 8px;margin-top: 8px;">
        <a-input v-if="opts.inputBox" v-model="inputValue"></a-input>
        <span v-else
          >{{ _value }}{{ opts.hasRate ? "%" : opts.hasPX ? "px" : "" }}</span
        >
      </a-col>
    </a-row>
  </div>
</template>

<script>
export default {
  name: "slider",
  props: {
    value: {
      required: true
    },
    styleKey: {
      type: String
    },
    opts: {
      type: Object,
      default: () => {
        return {
          hasPX: false,
          hasRate: false,
          placeholder: "",
          inputBox: false,
          min: 0,
          max: 100,
          step: 1
        };
      }
    }
  },
  data() {
    return {
      localValue: null,
      debounceValueChange: null
    };
  },
  computed: {
    _value: {
      get() {
        return parseFloat(this.localValue);
      },
      set(val) {
        val = parseFloat(val);
        this.localValue = val;
      }
    },

    inputValue: {
      get() {
        return this.opts.hasRate
          ? `${parseFloat(this.value)}%`
          : this.opts.hasPX
          ? `${parseFloat(this.value)}px`
          : this.value;
      },
      set(val) {
        if (val !== "" && val !== "px" && val !== "%") {
          this._value = val;
        }
      }
    }
  },
  watch: {
    _value(val) {
      if (val !== parseFloat(this.value)) {
        this.debounceValueChange(
          this.styleKey
            ? {
                key: this.styleKey,
                value: val
              }
            : val
        );
      }
    }
  },
  mounted() {
    // 创建局部value
    this.localValue = this.value;
    // 包装valueChange事件防止过度调用造成页面卡顿
    this.debounceValueChange = window.lodash.debounce(val => {
      this.$emit("valueChange", val);
    }, 20);
  }
};
</script>
