<template>
  <div class="wrap">
    <div class="titl">页面背景</div>
    <a-radio-group name="radioGroup" :defaultValue="1" class="radio-group">
      <a-radio :value="1">颜色</a-radio>
      <a-radio :value="2" disabled>图片</a-radio>
    </a-radio-group>
    <color-selector :color="background" @change="colorChange" />
  </div>
</template>

<script>
import colorSelector from "@/components/StyleMenuComponents/colorSelector.vue";
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState, mapGetters } = createNamespacedHelpers(
  "componentStore"
);
export default {
  name: "background-change",
  props: {
    background: {
      type: String,
      required: true
    }
  },
  components: {
    "color-selector": colorSelector
  },
  methods: {
    colorChange(val) {
      this.$emit("change", val);
    }
  },
  computed: {
    ...mapGetters(["activatedComponent"])
  }
};
</script>
<style lang="stylus" scoped>
.wrap
  .titl
    padding-bottom 18px
  .radio-group
    padding-bottom 12px
</style>
