<template>
  <div class="margin-edit-wrap">
    <div class="titl">调整边距</div>
    <slider
      v-for="item in items"
      :key="item.styleKey"
      :value="marginObject[item.styleKey]"
      :styleKey="item.styleKey"
      :opts="{ hasPX: true, placeholder: item.name, inputBox: true, max: 200 }"
      @valueChange="valueChange"
    />
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState, mapGetters } = createNamespacedHelpers(
  "componentStore"
);
import slider from "./slider.vue";
export default {
  name: "margin-edit",
  components: {
    slider
  },
  props: {
    customButtonMarginObject: {
      type: Object
    }
  },
  data() {
    return {
      items: [
        {
          styleKey: "margin-top",
          name: "上边距"
        },
        {
          styleKey: "margin-right",
          name: "右边距"
        },
        {
          styleKey: "margin-bottom",
          name: "下边距"
        },
        {
          styleKey: "margin-left",
          name: "左边距"
        }
      ]
    };
  },
  methods: {
    ...mapActions(["updateComponentInfo"]),
    valueChange(message) {
      this.$emit("valueChange", message);
    }
  },
  computed: {
    ...mapState(["activeComponentID"]),
    ...mapGetters(["activatedComponent"]),
    marginObject() {
      if (this.customButtonMarginObject) {
        return this.customButtonMarginObject;
      } else {
        return {
          "margin-top": parseInt(
            this.activatedComponent.componentInfo.style["margin-top"]
          ),
          "margin-right": parseInt(
            this.activatedComponent.componentInfo.style["margin-right"]
          ),
          "margin-bottom": parseInt(
            this.activatedComponent.componentInfo.style["margin-bottom"]
          ),
          "margin-left": parseInt(
            this.activatedComponent.componentInfo.style["margin-left"]
          )
        };
      }
    }
  }
};
</script>
<style lang="stylus" scoped>
.margin-edit-wrap
  .titl
    padding-bottom 18px
</style>
